"use client";

import React, { useState, useRef, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import { motion } from 'framer-motion';
import { Upload, FileText, Download } from 'lucide-react';
import mammoth from 'mammoth';

// Components
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Progress } from '@/components/ui/progress';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import TinyMCEEditor, { type TinyMCEEditorHandle } from '@/components/editor/TinyMCEEditor';
import ProofreadPdfViewer from '@/components/proofreader/ProofreadPdfViewer';

// Services and types
import { analyzePdfContent } from '@/lib/services/geminiService';
import { convertHtmlToPdf, htmlToWordBlob } from '@/lib/services/pdfWordConverter';
import type { Suggestion } from '@/lib/types/proofreader';
import { toast } from 'sonner';
import { useAppContext } from '@/lib/AppContext';

// Helper function to load pdfjs dynamically
async function loadPdfJs() {
  const pdfjs = await import('pdfjs-dist');
  if (typeof window !== 'undefined' && !pdfjs.GlobalWorkerOptions.workerSrc) {
    pdfjs.GlobalWorkerOptions.workerSrc = '/pdf.worker.mjs';
  }
  return pdfjs;
}

export default function ComplianceEditorPage() {
  const searchParams = useSearchParams();
  const documentId = searchParams.get('documentId');
  const { getDocumentPdfUrl, getDocumentContent, getDocument } = useAppContext();

  const [file, setFile] = useState<File | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [progress, setProgress] = useState(0);
  const [error, setError] = useState<string | null>(null);
  const [editorContent, setEditorContent] = useState<string>('');
  const [suggestions, setSuggestions] = useState<Suggestion[]>([]);
  const [selectedSuggestionIndex, setSelectedSuggestionIndex] = useState<number | null>(null);
  const [isEditorMode, setIsEditorMode] = useState(false);
  const [originalFileName, setOriginalFileName] = useState<string>('');
  const [fileType, setFileType] = useState<'pdf' | 'docx' | null>(null);
  const [pdfUrl, setPdfUrl] = useState<string | null>(null);
  const [selectedSuggestion, setSelectedSuggestion] = useState<Suggestion | null>(null);
  const [isConverting, setIsConverting] = useState(false);
  
  const editorRef = useRef<TinyMCEEditorHandle>(null);

  // Cleanup PDF URL on unmount
  useEffect(() => {
    return () => {
      if (pdfUrl) {
        URL.revokeObjectURL(pdfUrl);
      }
    };
  }, [pdfUrl]);

  // Load PDF URL from AppContext if documentId is provided
  useEffect(() => {
    if (!documentId) {
      console.log('[ComplianceEditor] No documentId provided');
      return;
    }

    console.log('[ComplianceEditor] Loading document:', documentId);
    setIsLoading(true);
    setError(null);

    try {
      // Get document metadata
      const document = getDocument(documentId);
      if (document) {
        setOriginalFileName(document.title || 'document');
        console.log('[ComplianceEditor] Document loaded:', document.title);
      }

      // Get PDF URL
      const url = getDocumentPdfUrl(documentId);
      console.log('[ComplianceEditor] PDF URL retrieved:', url ? 'exists' : 'null');

      if (url) {
        setPdfUrl(url);
        setFileType('pdf');
        console.log('[ComplianceEditor] PDF URL set successfully');
      } else {
        console.warn('[ComplianceEditor] No PDF URL found for document');
        setError('No PDF URL found for this document');
        setPdfUrl(null);
      }
    } catch (e) {
      console.error('[ComplianceEditor] Error loading PDF:', e);
      setError('Failed to load PDF');
      setPdfUrl(null);
    } finally {
      setIsLoading(false);
    }
  }, [documentId, getDocument, getDocumentPdfUrl]);

  // Load extracted text and fetch AI suggestions
  useEffect(() => {
    if (!documentId) return;

    const content = getDocumentContent(documentId);
    if (content) {
      console.log('[ComplianceEditor] Content loaded, analyzing...');
      setProgress(10);
      
      analyzePdfContent(content)
        .then(aiSuggestions => {
          setSuggestions(aiSuggestions);
          setProgress(100);
          toast.success(`Analysis complete! Found ${aiSuggestions.length} suggestions.`);
        })
        .catch(err => {
          console.error('[ComplianceEditor] Analysis error:', err);
          toast.error('Failed to analyze document');
        });
    }
  }, [documentId, getDocumentContent]);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0] || null;
    setFile(selectedFile);
    setSuggestions([]);
    setError(null);
    setEditorContent('');
    setIsEditorMode(false);
    // Clean up previous PDF URL
    if (pdfUrl) {
      URL.revokeObjectURL(pdfUrl);
      setPdfUrl(null);
    }
    if (selectedFile) {
      setOriginalFileName(selectedFile.name.replace(/\.(pdf|docx)$/i, ''));
    }
  };

  const handleUpload = async () => {
    if (!file) {
      setError('Please select a document to upload.');
      return;
    }

    setIsLoading(true);
    setError(null);
    setSuggestions([]);
    setProgress(10);

    try {
      const fileName = file.name.toLowerCase();
      const isPdf = fileName.endsWith('.pdf') || file.type === 'application/pdf';
      const isDocx = fileName.endsWith('.docx') || file.type.includes('officedocument.wordprocessingml');

      if (isPdf) {
        setFileType('pdf');
        setProgress(20);

        // Convert PDF to DOCX via backend
        const formData = new FormData();
        formData.append('file', file);

        const convertResponse = await fetch('/api/pdf-to-docx', {
          method: 'POST',
          body: formData,
        });

        if (!convertResponse.ok) {
          throw new Error('Failed to convert PDF to DOCX');
        }

        const docxBlob = await convertResponse.blob();
        setProgress(40);

        // Convert DOCX to HTML for TinyMCE
        const arrayBuffer = await docxBlob.arrayBuffer();
        const result = await mammoth.convertToHtml({ arrayBuffer });
        const html = result.value || '';
        
        setEditorContent(html);
        setIsEditorMode(true);
        
        setProgress(60);

        // Extract text from PDF for analysis
        const pdfArrayBuffer = await file.arrayBuffer();
        const pdfjs = await loadPdfJs();
        const pdf = await pdfjs.getDocument({ data: pdfArrayBuffer }).promise;
        let fullText = '';

        for (let i = 1; i <= pdf.numPages; i++) {
          const page = await pdf.getPage(i);
          const textContent = await page.getTextContent();
          const pageText = textContent.items.map((item: any) => item.str).join(' ');
          fullText += `--- PAGE ${i} ---\n${pageText}\n`;
          setProgress(60 + Math.round((i / pdf.numPages) * 20));
        }

        setProgress(85);

        // Analyze with AI
        const aiSuggestions = await analyzePdfContent(fullText);
        setSuggestions(aiSuggestions);
        
        setProgress(100);
        toast.success(`Document loaded! Found ${aiSuggestions.length} suggestions.`);
      } else if (isDocx) {
        setFileType('docx');
        setProgress(30);

        // Convert DOCX to HTML
        const arrayBuffer = await file.arrayBuffer();
        const result = await mammoth.convertToHtml({ arrayBuffer });
        const html = result.value || '';
        
        setEditorContent(html);
        setIsEditorMode(true);
        
        setProgress(60);

        // Extract text for analysis
        const tmp = document.createElement('div');
        tmp.innerHTML = html;
        const plainText = tmp.textContent || tmp.innerText || '';
        const textWithPages = `--- PAGE 1 ---\n${plainText}`;

        // Analyze with AI
        const aiSuggestions = await analyzePdfContent(textWithPages);
        setSuggestions(aiSuggestions);
        
        setProgress(100);
        toast.success(`Document loaded! Found ${aiSuggestions.length} suggestions.`);
      } else {
        throw new Error('Unsupported file type. Please upload a PDF or DOCX file.');
      }
    } catch (err) {
      console.error('Failed to process file:', err);
      setError(err instanceof Error ? err.message : 'Failed to process the file.');
      toast.error('Failed to process document');
    } finally {
      setIsLoading(false);
    }
  };


  const handleGoToSuggestion = (index: number) => {
    setSelectedSuggestionIndex(index);
    const suggestion = suggestions[index];
    if (!suggestion) return;

    if (isEditorMode && editorRef.current) {
      // Scroll to and highlight the text in editor (use text field, fallback to original for compatibility)
      const textToHighlight = suggestion.text || suggestion.original || '';
      if (textToHighlight) {
        editorRef.current.highlightText(textToHighlight);
      }
    } else if (pdfUrl && fileType === 'pdf') {
      // Navigate to PDF page and highlight
      setSelectedSuggestion(suggestion);
    }
  };


  const handleApplySuggestion = (index: number) => {
    const suggestion = suggestions[index];
    if (!suggestion || !editorRef.current || !isEditorMode) return;

    // Replace text in editor (use text field, fallback to original for compatibility)
    const textToReplace = suggestion.text || suggestion.original || '';
    if (!textToReplace) {
      toast.error('No text to replace');
      return;
    }

    editorRef.current.replaceText(textToReplace, suggestion.suggestion);
    
    // Update editor content from editor
    const newContent = editorRef.current.getContent();
    setEditorContent(newContent);
    
    // Remove applied suggestion from list
    setSuggestions(prev => prev.filter((_, i) => i !== index));
    
    // Adjust selected index if needed
    if (selectedSuggestionIndex !== null) {
      if (selectedSuggestionIndex === index) {
        setSelectedSuggestionIndex(null);
      } else if (selectedSuggestionIndex > index) {
        setSelectedSuggestionIndex(selectedSuggestionIndex - 1);
      }
    }
    
    toast.success('Suggestion applied');
  };

  const handleSaveAsWord = async () => {
    if (!editorRef.current || !isEditorMode) {
      toast.error('No content to save');
      return;
    }

    try {
      // Get current content from editor
      const content = editorRef.current.getContent();
      const blob = await htmlToWordBlob(content);
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `${originalFileName || 'document'}_edited.docx`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      
      toast.success('Document downloaded as Word');
    } catch (err) {
      console.error('Save error:', err);
      toast.error('Failed to save as Word');
    }
  };

  const handleSaveAsPdf = async () => {
    if (!editorRef.current || !isEditorMode) {
      toast.error('No content to save');
      return;
    }

    try {
      // Get current content from editor
      const content = editorRef.current.getContent();
      const pdfDataUrl = await convertHtmlToPdf(content);
      const a = document.createElement('a');
      a.href = pdfDataUrl;
      a.download = `${originalFileName || 'document'}_edited.pdf`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      
      toast.success('Document downloaded as PDF');
    } catch (err) {
      console.error('Save error:', err);
      toast.error('Failed to save as PDF');
    }
  };

  const handleConvertToEditor = async () => {
    if (!pdfUrl) {
      toast.error('PDF not available for conversion');
      return;
    }

    setIsConverting(true);
    setError(null);
    setProgress(10);

    try {
      // Convert PDF to DOCX via backend
      const response = await fetch(pdfUrl);
      const blob = await response.blob();
      const formData = new FormData();
      formData.append('file', blob, 'document.pdf');

      setProgress(30);

      const convertResponse = await fetch('/api/pdf-to-docx', {
        method: 'POST',
        body: formData,
      });

      if (!convertResponse.ok) {
        throw new Error('Failed to convert PDF to DOCX');
      }

      const docxBlob = await convertResponse.blob();
      setProgress(60);

      // Convert DOCX to HTML for TinyMCE
      const arrayBuffer = await docxBlob.arrayBuffer();
      const result = await mammoth.convertToHtml({ arrayBuffer });
      const html = result.value || '';
      
      setEditorContent(html);
      setIsEditorMode(true);
      setProgress(100);
      
      toast.success('Document converted to editor mode');
    } catch (err) {
      console.error('Conversion error:', err);
      setError(err instanceof Error ? err.message : 'Failed to convert PDF');
      toast.error('Failed to convert PDF to editor mode');
    } finally {
      setIsConverting(false);
    }
  };

  // Add debugging
  useEffect(() => {
    console.log('[ComplianceEditor] State:', {
      file: file?.name,
      fileType,
      isEditorMode,
      pdfUrl: pdfUrl ? 'set' : 'not set',
      suggestionsCount: suggestions.length,
      isLoading
    });
  }, [file, fileType, isEditorMode, pdfUrl, suggestions.length, isLoading]);

  // Group suggestions by category
  const groupedSuggestions = suggestions.reduce((acc, suggestion, index) => {
    const category = suggestion.category || 'compliance';
    if (!acc[category]) {
      acc[category] = [];
    }
    acc[category].push({ ...suggestion, index });
    return acc;
  }, {} as Record<string, Array<Suggestion & { index: number }>>);

  const getSeverityColor = (severity: string) => {
    switch (severity) {
      case 'critical':
        return 'destructive';
      case 'warning':
        return 'default';
      default:
        return 'secondary';
    }
  };

  const getCategoryLabel = (category: string) => {
    switch (category) {
      case 'compliance':
        return 'Compliance';
      case 'grammar':
        return 'Grammar';
      case 'style':
        return 'Style';
      default:
        return category;
    }
  };

  if (isLoading) {
    return (
      <div className="flex flex-col items-center justify-center h-screen bg-zinc-50 dark:bg-zinc-950 text-center gap-4 px-6">
        <Card className="w-full max-w-md border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900">
          <CardHeader>
            <CardTitle className="text-gray-900 dark:text-gray-50">Analyzing Document...</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <Progress value={progress} />
            <p className="text-sm text-gray-600 dark:text-gray-400">
              {progress < 30 ? 'Loading document...' : 
               progress < 70 ? 'Extracting text...' :
               'Analyzing compliance...'}
            </p>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen bg-zinc-50 dark:bg-zinc-950 p-4 sm:p-6 lg:p-8"
    >
      <div className="max-w-7xl mx-auto">
        <div className="mb-6">
          <h1 className="text-3xl font-bold text-gray-900 dark:text-gray-50 mb-2">
            Financial Compliance Editor
          </h1>
          <p className="text-gray-600 dark:text-gray-400">
            Upload documents for FINRA/SEC compliance review and editing
          </p>
        </div>

        {!isEditorMode && !file && !pdfUrl && !documentId && (
          <Card className="border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900">
            <CardHeader>
              <CardTitle className="text-gray-900 dark:text-gray-50">Upload Document</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {error && (
                <Alert variant="destructive">
                  <AlertTitle>Error</AlertTitle>
                  <AlertDescription>{error}</AlertDescription>
                </Alert>
              )}
              
              <div className="space-y-2">
                <Label htmlFor="file-upload">Select PDF or Word Document</Label>
                <div className="flex gap-3">
                  <Input
                    id="file-upload"
                    type="file"
                    accept=".pdf,.docx,application/pdf,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                    onChange={handleFileChange}
                  />
                  <Button onClick={handleUpload} disabled={!file}>
                    <Upload className="h-4 w-4 mr-2" />
                    Analyze
                  </Button>
                </div>
                <p className="text-xs text-gray-500 dark:text-gray-400">
                  Supported formats: PDF, DOCX (up to 10MB)
                </p>
              </div>
            </CardContent>
          </Card>
        )}

        {(isEditorMode || file || pdfUrl) && (
          <>
            <div className="mb-4 flex justify-between items-center">
              <div className="flex items-center gap-2">
                <Badge variant="outline">{file?.name || originalFileName || 'Document'}</Badge>
                {suggestions.length > 0 && (
                  <Badge variant="secondary">{suggestions.length} suggestions found</Badge>
                )}
              </div>
              <div className="flex gap-2">
                {pdfUrl && fileType === 'pdf' && !isEditorMode && (
                  <Button
                    variant="default"
                    onClick={handleConvertToEditor}
                    disabled={isConverting}
                  >
                    {isConverting ? 'Converting...' : 'Edit Document'}
                  </Button>
                )}
                <Button
                  variant="outline"
                  onClick={() => {
                    setFile(null);
                    setIsEditorMode(false);
                    setSuggestions([]);
                    setEditorContent('');
                    setFileType(null);
                    setPdfUrl(null);
                    setSelectedSuggestion(null);
                  }}
                >
                  <Upload className="h-4 w-4 mr-2" />
                  New Document
                </Button>
              </div>
            </div>
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
              {/* Document Viewer/Editor Panel */}
              <div className="lg:col-span-2">
                {isEditorMode ? (
                  <Card className="border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900">
                    <CardHeader className="pb-2">
                      <div className="flex items-center justify-between">
                        <CardTitle className="text-gray-900 dark:text-gray-50">
                          Document Editor
                        </CardTitle>
                        <div className="flex gap-2">
                          <Button 
                            variant="outline" 
                            size="sm" 
                            onClick={handleSaveAsWord}
                          >
                            <Download className="h-4 w-4 mr-2" />
                            Download as Word
                          </Button>
                          <Button 
                            variant="outline" 
                            size="sm" 
                            onClick={handleSaveAsPdf}
                          >
                            <Download className="h-4 w-4 mr-2" />
                            Download as PDF
                          </Button>
                        </div>
                      </div>
                    </CardHeader>
                    <CardContent className="p-0">
                      <TinyMCEEditor
                        ref={editorRef}
                        content={editorContent}
                        onContentChange={setEditorContent}
                        suggestions={suggestions}
                        selectedSuggestionIndex={selectedSuggestionIndex}
                      />
                    </CardContent>
                  </Card>
                ) : pdfUrl && fileType === 'pdf' ? (
                  <ProofreadPdfViewer
                    pdfUrl={pdfUrl}
                    suggestions={suggestions}
                    selectedSuggestion={selectedSuggestion}
                    onSuggestionClick={setSelectedSuggestion}
                    onApplyEdit={(pageNumber, originalText, newText) => {
                      // Handle edit application - for now just show a toast
                      toast.info('Convert to editor mode to apply changes');
                    }}
                  />
                ) : (
                  <Card className="border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900">
                    <CardContent className="p-6 text-center text-gray-600 dark:text-gray-400">
                      <FileText className="h-12 w-12 mx-auto mb-4 opacity-50" />
                      <p>Upload a document to get started</p>
                    </CardContent>
                  </Card>
                )}
              </div>

            {/* Suggestions Panel */}
            <div className="lg:col-span-1">
              <Card className="border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900 sticky top-6">
                <CardHeader>
                  <CardTitle className="text-gray-900 dark:text-gray-50 flex items-center justify-between">
                    <span>Suggestions</span>
                    <Badge variant="secondary">{suggestions.length}</Badge>
                  </CardTitle>
                </CardHeader>
                <CardContent className="max-h-[calc(100vh-200px)] overflow-y-auto space-y-4">
                  {suggestions.length === 0 ? (
                    <div className="text-center py-8 text-gray-500 dark:text-gray-400">
                      <p>No suggestions found</p>
                      <p className="text-sm mt-2">Your document looks good!</p>
                    </div>
                  ) : (
                    <>
                      {Object.entries(groupedSuggestions).map(([category, items]) => (
                        <div key={category}>
                          <div className="flex items-center gap-2 mb-3">
                            <Badge variant="outline" className="text-xs">
                              {getCategoryLabel(category)}
                            </Badge>
                            <Separator className="flex-1" />
                          </div>
                          
                          <div className="space-y-3">
                            {items.map((item) => (
                              <Card 
                                key={item.index}
                                className={`border cursor-pointer transition-colors ${
                                  selectedSuggestionIndex === item.index
                                    ? 'border-emerald-500 dark:border-emerald-600 bg-emerald-50 dark:bg-emerald-950'
                                    : 'border-zinc-200 dark:border-zinc-700'
                                }`}
                                onClick={() => handleGoToSuggestion(item.index)}
                              >
                                <CardContent className="p-3 space-y-2">
                                  <div className="flex items-start justify-between gap-2">
                                    <Badge variant={getSeverityColor(item.severity || 'warning') as any} className="text-xs">
                                      {item.severity || 'warning'}
                                    </Badge>
                                    <span className="text-xs text-gray-500">Page {item.page}</span>
                                  </div>
                                  
                                  <p className="text-sm text-gray-700 dark:text-gray-300">
                                    {item.issue || item.explanation || 'Issue found'}
                                  </p>
                                  
                                  <div className="text-xs space-y-1">
                                    <p className="text-red-600 dark:text-red-400">
                                      <span className="font-semibold">Original:</span> {item.text || item.original || ''}
                                    </p>
                                    <p className="text-green-600 dark:text-green-400">
                                      <span className="font-semibold">Suggested:</span> {item.suggestion}
                                    </p>
                                  </div>
                                  
                                  {isEditorMode && (
                                    <Button
                                      variant="default"
                                      size="sm"
                                      className="w-full mt-2"
                                      onClick={(e) => {
                                        e.stopPropagation();
                                        handleApplySuggestion(item.index);
                                      }}
                                    >
                                      Apply Change
                                    </Button>
                                  )}
                                </CardContent>
                              </Card>
                            ))}
                          </div>
                        </div>
                      ))}
                    </>
                  )}
                </CardContent>
              </Card>
            </div>
          </div>
          </>
        )}
      </div>
    </motion.div>
  );
}


